import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import Loader from '../components/Loader';

import { LocationQuery } from '../api';
import { useSnackbar } from '../hooks';
import { CreateLocation } from '../utils/dataTypes';

export default function ViewLocationForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['location', pathname.split('/')[3]],
    queryFn: LocationQuery,
    keepPreviousData: true,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateLocation>({
    initialValues: {
      id: '',
      zipCode: '',
      name: '',
      stateCode: '',
      stateName: '',
      location: [NaN, NaN],
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  const handleEdit = () => {
    navigate(pathname.replace('view', 'edit'), { replace: true });
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <Box key="name" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="name"
                  name="name"
                  label="Name"
                  value={formik.values.name}
                  disabled
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={formik.touched.name && Boolean(formik.errors.name)}
                  helperText={formik.touched.name && formik.errors.name}
                />
              </Box>
              <Box key="stateName" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="stateName"
                  name="stateName"
                  label="State Name"
                  value={formik.values.stateName}
                  disabled
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.stateName && Boolean(formik.errors.stateName)
                  }
                  helperText={
                    formik.touched.stateName && formik.errors.stateName
                  }
                />
              </Box>
              <Box key="latitude" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="latitude"
                  name="location[0]"
                  label="Latitude"
                  value={
                    isNaN(formik.values.location[0])
                      ? ''
                      : formik.values.location[0]
                  }
                  disabled
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.location &&
                    Boolean(formik.errors.location && formik.errors.location[0])
                  }
                  helperText={
                    formik.touched.location &&
                    formik.errors.location &&
                    formik.errors.location[0]
                  }
                />
              </Box>
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%', height: '75px' }}>
              <Box key="zipCode" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="zipCode"
                  name="zipCode"
                  label="Zip Code"
                  value={formik.values.zipCode}
                  disabled
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.zipCode && Boolean(formik.errors.zipCode)
                  }
                  helperText={formik.touched.zipCode && formik.errors.zipCode}
                />
              </Box>
              <Box key="stateCode" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="stateCode"
                  name="stateCode"
                  label="State Code"
                  value={formik.values.stateCode}
                  disabled
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.stateCode && Boolean(formik.errors.stateCode)
                  }
                  helperText={
                    formik.touched.stateCode && formik.errors.stateCode
                  }
                />
              </Box>
              <Box key="longitude" sx={{ height: '75px' }}>
                <TextField
                  fullWidth
                  size="small"
                  id="longitude"
                  name="location[1]"
                  label="Longitude"
                  value={
                    isNaN(formik.values.location[1])
                      ? ''
                      : formik.values.location[1]
                  }
                  disabled
                  onChange={formik.handleChange}
                  onBlur={formik.handleBlur}
                  error={
                    formik.touched.location &&
                    Boolean(formik.errors.location && formik.errors.location[1])
                  }
                  helperText={
                    formik.touched.location &&
                    formik.errors.location &&
                    formik.errors.location[1]
                  }
                />
              </Box>
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
